.header {
  flex-direction: column;
  background-color: #ffd1ac;
  &__desc {
    display: inline-block;
    text-align: center;
    text-transform: uppercase;
    width: 100%;
    padding: s-md(9) s-md(14) s-md(8);
    font-size: s-md(12);
    background-color: var(--orange);
  }
  &__logo {
    @include block(s-md(218), auto);
    margin: s-md(18) 0;
  }
  @include mobile {
    @include flex-50-y();
  }
}
