.hero {
  position: relative;
  &__img {
    @include img(100%);
  }
  &__content {
    position: absolute;
    @include flex-50-y;
    justify-content: space-between;
    width: 100%;
    left: 0;
    bottom: 0;
    padding: 0 s-md(13) s-md(13);
  }
  &__timer,
  &__sale {
    @include flex-50-y;
    flex-direction: column;
    text-transform: uppercase;
    font-weight: 700;
    color: var(--white);
  }
  &__timer {
    gap: s-md(9);
    border-radius: s-md(7);
    padding: s-md(5) s-md(7) s-md(12);
    background-color: var(--orange);
    &--title {
      font-size: s-md(10);
      letter-spacing: 0.02em;
    }
    &--time {
      font-size: s-md(21);
    }
  }
  &__sale {
    gap: s-md(10);
    border-radius: s-md(6);
    padding: s-md(7) s-md(7) s-md(10);
    background-color: var(--green);
    &--old {
      position: relative;
      font-size: s-md(18);
      &::after {
        position: absolute;
        content: '';
        top: 50%;
        left: 0;
        @include block(100%, s-md(3));
        background-color: #e27500;
        transform: translateY(-50%);
      }
    }
    &--new {
      font-size: s-md(22);
    }
  }
}
