// Перерасчёт из px в rem на ширине экрана 1920px
@function s($px) {
  @return calc($px / 10) + rem;
}
// Перерасчёт из px в rem на ширине экрана 768px
@function s-md($px) {
  @return calc(($px / 10) * 2) + rem;
}

//                                                            Подключение mixins
@import 'mixin';
//                                                            Подключение основных переменных
@import 'static/variables';
//                                                            Подключение шрифтов
@import 'static/fonts';

// // Основной шрифт
$font-family: 'Gotham Pro';

$fonts: 'Regular' 400, 'Bold' 700;

@each $name, $weight in $fonts {
  @include font('GothamPro-' + $name, $font-family, $weight);
}

html,
body {
  user-select: none;
  margin: 0 auto;
  padding: 0;
  font-family: $font-family, sans-serif; // шрифт по умолчанию
  font-weight: 400; // жирность шрифта по умолчанию
  font-style: normal;
  line-height: 100%;
  letter-spacing: 0.01em;
}

html {
  font-size: 0.5208335vw; // на ширине экрана 1920px (0.5208335vw === 10px)
  @include mobile {
    font-size: 5px;
    font-size: 1.5625vw;
    font-size: calc(100 / 390) * 5vw; // где $breakpoint-mobile - это ширина мобильной версии в px
    -webkit-text-size-adjust: none;
  }
}

body {
  overflow-x: hidden;
  font-size: s(16); // размер шрифта по умолчанию (16px)
  color: var(--black); // цвет текста по умолчанию
  background-color: var(--white); // заливка по умолчанию
  @include mobile {
    font-size: s-md(16); // размер шрифта по умолчанию (16px)
    -webkit-text-size-adjust: none;
  }
}

//                                                            Подключение сброса стандартных стилей
@import 'static/reset';

main {
  width: 100%;
  max-width: 390px;
  margin: 0 auto;
}

.container {
  width: s(390);
  margin: 0 auto;
  @include mobile {
    width: 100%;
    padding: 0 s-md(16);
  }
}

.desktop {
  @include mobile {
    display: none;
  }
}

.mobile {
  display: none;
  @include mobile {
    display: initial;
  }
}

.desc-mobile {
  @include flex-50-50;
  height: 100vh;
  width: 100vw;
  font-size: s(50);
  font-weight: 700;
  @include mobile {
    display: none;
  }
}

@import './header';
